import requests from './requests'

// user login api
export const userLogin = (application_id, login_type, login_id, login_secret, device_id) =>
    requests({
        url: '/user/login',
        method: 'post',
        data: {
            application_id,
            login_type,
            login_id,
            login_secret,
            device_id,
        },
    })

// get account info api
// method: 'post',
// data: target_id, target_type
// headers: Authorization
export const getAccount = (target_id, target_type) =>
    requests({
        url: '/user/get_account',
        method: 'post',
        data: {
            target_id,
            target_type,
        },
    })

