//cart store

const state = {
    cart: [],
    cartTotal: 0,
}

const mutations = {
    ADD_TO_CART(state, payload) {
        state.cart.push(payload)
        console.log('state.cart', state.cart)
    },
    SET_CART(state, payload) {
        state.cart = payload
    }
}

const actions = {
    addToCart({ commit }, payload) {
        commit('ADD_TO_CART', payload)
    },
    getCart({ commit }) {
        commit('GET_CART')
    }
}

const getters = {}

export default {
    namespaced: true,
    state,
    mutations,
    actions,
    getters,
}
