const state = {
    planInfo: {},

    accountInfo: {
        firstName: '',
        lastName: '',
        email: '',
        password: '',
    },

    addressInfo: {
        street: '',
        apt: '',
        city: '',
        state: '',
        zip: '',
    },

    paymentInfo: {
        cardName: '',
        cardNumber: '',
        cardExpiry: '',
    },

    numberInfo: {
        number: '',
    },

    stepper: 1,
    accountError: 200,
    numberError: 200,
}
const mutations = {
    SET_PLAN_INFO(state, payload) {
        state.planInfo = payload
        console.log('@SET_PLAN_INFO', state.planInfo)
    },
    SET_ACCOUNT(state, data) {
        state.accountInfo = data
        console.log('@setAccount: ', state.accountInfo)
    },
    SET_ADDRESS(state, data) {
        state.addressInfo = data
        console.log('@setAddress: ', state.addressInfo)
    },
    SET_CARD(state, data) {
        state.paymentInfo = data
        console.log('@setCard: ', state.paymentInfo)
    },
    SET_NUMBER(state, data) {
        state.numberInfo = data
        console.log('@setNumber: ', state.numberInfo)
    },
    SET_STEPPER(state, step) {
        state.stepper = step
    },
    SET_ACCOUNT_ERROR(state, error) {
        state.accountError = error
    },
    SET_NUMBER_ERROR(state, error) {
        state.numberError = error
    },
    RESET_MODEL(state) {
        state.accountInfo = {
            firstName: '',
            lastName: '',
            email: '',
            password: '',
        }
        state.addressInfo = {
            street: '',
            apt: '',
            city: '',
            state: '',
            zip: '',
        }
        state.paymentInfo = {
            cardName: '',
            cardNumber: '',
            cardExpiry: '',
        }
        state.numberInfo = {
            number: '',
        }
        state.stepper = 1
        state.accountError = 200
        state.numberError = 200
    },
}
const actions = {
    setPlanInfo({ commit }, payload) {
        commit('SET_PLAN_INFO', payload)
    },
    setAccount({ commit }, data) {
        commit('SET_ACCOUNT', data)
    },
    setAddress({ commit }, data) {
        commit('SET_ADDRESS', data)
    },
    setCard({ commit }, data) {
        commit('SET_CARD', data)
    },
    setNumber({ commit }, data) {
        commit('SET_NUMBER', data)
    },
    setStepper({ commit }, step) {
        commit('SET_STEPPER', step)
    },
    setAccountError({ commit }, error) {
        console.log('@setAccountError: ', error)
        commit('SET_ACCOUNT_ERROR', error)
    },
    setNumberError({ commit }, error) {
        console.log('@setNumberError: ', error)
        commit('SET_NUMBER_ERROR', error)
    },
    resetModel({ commit }) {
        commit('RESET_MODEL')
    },

    // add account
}
const getters = {}

export default {
    namespaced: true,
    state,
    mutations,
    actions,
    getters,
}
