<template>
    <div>
        <SubHeader>
            <template #title>Shop</template>
            <template #subtitle>
                Lorem ipsum dolor sit amet, consectetur adipiscing elit.
                <br />In congue ante ac nunc bibendum volutpat. <br />
                Aenean iaculis sem utvelit lobortis.
            </template>
        </SubHeader>
        <v-container fluid class="mt-4 mb-12 max-w-7xl relative">
            <v-row class="gap-24">
                <v-col cols="auto">
                    <div
                        class="ml-4 font-semibold text-xl leading-7"
                        v-show="currentParentCategory !== 'Uncategorized'"
                    >
                        Filters
                    </div>
                    <v-sheet
                        elevation="0"
                        width="190px"
                        v-show="currentParentCategory !== 'Uncategorized'"
                        class="flex flex-col items-start p-0 gap-4 mt-4 ml-4"
                        color="bgColor"
                    >
                        <div class="text-sm font-bold">Categories</div>
                        <v-skeleton-loader
                            v-if="loading"
                            class="mx-auto"
                            width="220px"
                            type="image"
                        ></v-skeleton-loader>
                        <div
                            v-else
                            v-for="(item, index) in items"
                            :key="`item-${index}`"
                        >
                            <FTCheckbox v-model="selected" :label="item">
                                {{ item }}
                            </FTCheckbox>
                        </div>
                    </v-sheet>
                </v-col>

                <v-col>
                    <div>
                        <div class="flex justify-end mr-4 p-0">
                            <div>
                                <v-select
                                    v-model="selectedSort"
                                    :items="sortOptions"
                                    label="Sort by"
                                    outlined
                                    dense
                                    class="w-48"
                                ></v-select>
                            </div>
                        </div>
                        <div v-if="offersToArray" class="text-end mr-4 mb-4">
                            Showing {{ offersToArray.length }} Products
                        </div>
                        <div v-else class="text-end mr-4 mb-4">Loading...</div>

                        <div
                            class="
                                flex flex-row
                                items-start
                                p-0
                                flex-none
                                order-none
                                flex-grow-0
                                gap-5
                            "
                            v-for="indexRow in Math.ceil(
                                this.offersToArray.length / 3
                            )"
                            :key="`row-${indexRow}`"
                        >
                            <FTCard
                                :title="item.data.name"
                                image="https://placeimg.com/300/300/tech"
                                :description="item.data.description"
                                :price="Number(item.data.amounts.primary)"
                                :link="'/plans/' + item.id"
                                v-for="(item, index) in offersToArray.slice(
                                    0 + (indexRow - 1) * 3,
                                    3 + (indexRow - 1) * 3
                                )"
                                :key="`offers-${index}`"
                            />
                        </div>
                    </div>
                </v-col>
            </v-row>
            <v-snackbar v-model="snackbar" timeout="2000">
                Added {{ addedItem }} to cart
                <template v-slot:action="{ attrs }">
                    <!-- button redirect to cart -->
                    <v-btn
                        color="primary"
                        text
                        v-bind="attrs"
                        @click="goToCart"
                    >
                        Go to cart
                    </v-btn>
                </template>
            </v-snackbar>
        </v-container>
    </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import FTCard from "@/components/FTCard.vue";
// import FTCategoriesFilter from "@/components/FTCategoriesFilter.vue";
import FTCheckbox from "@/components/FTCheckbox.vue";
import SubHeader from "@/components/SubHeader.vue";
export default {
    name: "NewPlans",
    components: {
        FTCard,
        // FTCategoriesFilter,
        FTCheckbox,
        SubHeader,
    },
    data() {
        return {
            items: [],
            stores: [],
            // plans: [],
            // toggle_exclusive: 0,
            selected: {
                checked: [],
                disabled: [],
            },
            snackbar: false,
            addedItem: null,
            itemLimit: 3,
            sortOptions: ["Price: Low to High", "Price: High to Low"],
            selectedSort: "Price: Low to High",
        };
    },
    computed: {
        // get plans/offers from store
        ...mapState({
            offers: (state) => state.home.offers,
            filteredOffers: (state) => state.home.filteredOffers,
            parentCategories: (state) => state.home.parentCategories,
            subCategories: (state) => state.home.subCategories,
            currentParentCategory: (state) => state.home.currentParentCategory,
            loading: (state) => state.home.loading,
        }),

        // get the category names
        categoryNames() {
            return Object.values(this.subCategories);
        },

        offersToArray() {
            // convert object to array
            let array = Object.values(this.filteredOffers);
            // add image to offer 
            // array.forEach((item) => {
            //     if (item.data.name === "Google Pixel 6a") {
            //         item.image =
            //             "https://lh3.googleusercontent.com/PLbSa6bjXaL_xvNsjLGsWAKWcLIe4wNPacQDYQjK4R3zR0zX3EkOV5U54N1keyVW8fhxZDcXyXEPrx15AnBXIpohisg1tB8_F5g=w1014";
            //     } else if (item.data.name === "iPhone 13") {
            //         item.image =
            //             "https://pngimg.com/uploads/iphone_13/iphone_13_PNG29.png";
            //     }
            // });
            return array;
        },
    },
    methods: {
        ...mapActions("home", [
            "getOffers",
            "getParentCategories",
            "getSubCategories",
            "getFilteredOffers",
        ]),
        // ...mapActions('cart', ['addToCart']),
        ...mapActions("checkout", ["setPlanInfo"]),
        // 
        getPlan(item) {
            console.log("@getPlan: ", item);
            this.setPlanInfo(item);
            this.$router.push({
                name: "RwCheckout",
                // query: {
                //     title: item.data.name,
                //     price: item.data.amounts.primary,
                //     lines: item.data.num_of_lines,
                //     entityID: item.id,
                // },
            });
        },
        // 
        addToCart(item) {
            // debugger
            this.snackbar = true;
            this.addedItem = item.data.name;
            console.log("@addToCart: ", item);
            // add item to session storage
            let cart = JSON.parse(sessionStorage.getItem("cart"));
            if (cart === null) {
                cart = [];
            }
            // 1, , 1
            let index = cart.findIndex((i) => i.id === item.id);
            if (index === -1) {
                item.quantity = 1;
                cart.push(item);
            } else {
                cart[index].quantity += 1;
            }

            // save to session storage
            sessionStorage.setItem("cart", JSON.stringify(cart));
            console.log("cart: ", cart);
        },
        filterCheckbox() {
            this.getFilteredOffers(this.selected.checked);
        },
        goToCart() {
            this.$router.push({
                name: "RwCart",
            });
        },
    },
    watch: {
        // selected.checked categorywatch selected.checkedcheckboxselected.checked
        "selected.checked": {
            handler() {
                this.filterCheckbox();
                // console.log("selected.checked: ", this.selected.checked);
                // console.log("newWal: ", newWal);
                // console.log("oldVal: ", oldVal);
            },
        },
        // subCategoriescategorysubCategories
        subCategories(newValue) {
            this.selected.checked = [...Object.values(newValue), "Other"];
            this.items = this.selected.checked;
            this.filterCheckbox();
        },
    },
    async mounted() {
        await this.getOffers();
        const currentParentCategoryID = Object.keys(this.parentCategories).find(
            (key) => this.parentCategories[key] === this.currentParentCategory
        );
        if (currentParentCategoryID) {
            await this.getSubCategories(currentParentCategoryID);
        } else if (this.currentParentCategory === "Uncategorized") {
            await this.getFilteredOffers("Uncategorized");
        } else {
            await this.getSubCategories(Object.keys(this.parentCategories)[0]);
        }
    },
    created: async function () {
        // await this.$axios
        //     .get(this.$strapi_url + "stores?populate=*")
        //     .then((res) => {
        //         this.stores = res.data;
        //     });
    },
};
</script>

<style scoped>
.rounded-card {
    border-radius: 100px;
}
.set-padding {
    padding: 8px;
}
