import { getOffers } from '@/api'

const state = {
    offers: {},
    parentCategories: {},
    subCategories: {},
    filteredOffers: {},
    currentParentCategory: null,
    loading: false,
    drawer: false,
}
const mutations = {
    SET_OFFERS(state, offers) {
        state.offers = offers
        console.log('SET_OFFERS', state.offers)
    },
    SET_PARENT_CATEGORIES(state, parentCategories) {
        state.parentCategories = parentCategories
        // console.log('SET_PARENT_CATEGORIES', state.parentCategories)
    },
    SET_SUB_CATEGORIES(state, subCategories) {
        state.subCategories = subCategories
        // console.log('SET_SUB_CATEGORIES', state.subCategories)
    },
    SET_FILTERED_OFFERS(state, filteredOffers) {
        state.filteredOffers = filteredOffers
        // console.log('SET_FILTERED_OFFERS', state.filteredOffers)
    },
    SET_DRAWER(state, drawer) {
        state.drawer = drawer
    },
    SET_CURRENT_PARENT_CATEGORY(state, currentParentCategory) {
        state.currentParentCategory = currentParentCategory
    },
}
const actions = {
    async getOffers({ commit }) {
        state.loading = true
        const offers = await getOffers('dishb2b')
        state.loading = false
        commit('SET_OFFERS', offers)
    },
    getParentCategories({ commit }) {
        const offers = state.offers
        const parentCategories = {}
        // Loop through the offers.category_by_id object to get category data
        for (const key in offers.category_by_id) {
            // If the parent id does not exist, it is a parent category
            if (!offers.category_by_id[key].data.parent) {
                parentCategories[key] = offers.category_by_id[key].data.name
            }
        }
        // sort the parent categories by sort_priority
        const sortedParentCategories = {}
        Object.keys(parentCategories)
            .sort((a, b) => offers.category_by_id[a].data.sort_priority - offers.category_by_id[b].data.sort_priority)
            .forEach((key) => {
                sortedParentCategories[key] = parentCategories[key]
            })
        commit('SET_PARENT_CATEGORIES', sortedParentCategories)
    },
    getSubCategories({ commit }, parentCategoryId) {
        // debugger
        // console.log('@vuex getSubCategories', parentCategoryId)
        const offers = state.offers
        // console.log('@vuex getSubCategories offers', offers)
        const subCategories = {}
        // Loop through the offers.category_by_id object to get category data
        for (const key in offers.category_by_id) {
            // if the parent category id is the same as the user's selected parent category id, then put this category into the sub category
            if (offers.category_by_id[key].data?.parent === parentCategoryId) {
                subCategories[key] = offers.category_by_id[key].data.name
            }
        }
        // sort the sub categories by alphabetical order
        const sortedSubCategories = {}
        Object.keys(subCategories)
            .sort((a, b) => offers.category_by_id[a].data.name.localeCompare(offers.category_by_id[b].data.name))
            .forEach((key) => {
                sortedSubCategories[key] = subCategories[key]
            })
        commit('SET_SUB_CATEGORIES', sortedSubCategories)
    },

    getFilteredOffers({ commit }, categories) {
        // console.log('@getFilteredOffers categories: ', categories)
        const offers = state.offers
        const subCategories = state.subCategories
        const filteredOffers = {}
        const categoriesIDs = []

        if (categories === 'Uncategorized') {
            // state.currentParentCategory = 'Uncategorized'
            for (const key in offers.offer_by_id) {
                if (!offers.offer_by_id[key].data?.categories) {
                    filteredOffers[key] = offers.offer_by_id[key]
                }
            }
        } else {
            // Loop through the offers.category_by_id object to get subCategory ids
            for (const key in subCategories) {
                if (categories.includes(subCategories[key])) {
                    categoriesIDs.push(key)
                } else {
                    console.log('not found in subCategories')
                }
            }
            // console.log('categoriesIds', categoriesIDs)
            // categoriesId is an array, which contains the sub categories id that the user selected
            // loop through offers.offer_by_id object to get offer data
            for (const key in offers.offer_by_id) {
                if (offers.offer_by_id[key].data?.categories) {
                    // If the offer has categories and the categories are the same as the user selected
                    // Check if the two arrays have an intersection
                    if (offers.offer_by_id[key].data.categories.some((v) => categoriesIDs.includes(v))) {
                        // filteredOffers[key] = offers.offer_by_id[key].data
                        filteredOffers[key] = offers.offer_by_id[key]
                    }
                } else {
                    // console.log('not found in offers.offer_by_id')
                }
            }
        }
        commit('SET_FILTERED_OFFERS', filteredOffers)
    },
    getCurrentParentCategory({ commit }, parentCategory) {
        commit('SET_CURRENT_PARENT_CATEGORY', parentCategory)
    },
    // drawer
    toggleDrawer({ commit }) {
        commit('SET_DRAWER', !state.drawer)
    },
}
const getters = {}

export default {
    namespaced: true,
    state,
    mutations,
    actions,
    getters,
}
