<template>
    <div class="flex flex-row">
        <!-- <div
            class="text-sm font-bold text-black cursor-pointer bg-green-500"
            @click="toggle()"
        >
            {{ label }}
        </div> -->
        <input
            type="checkbox"
            :checked="selected.checked.includes(label)"
            :disabled="selected.disabled.includes(label)"
            class="opacity-0 absolute h-5 w-5 cursor-pointer"
            @change="toggle()"
        />
        <div
            class="
                bg-flareTelBg
                border border-black
                w-5
                h-5
                flex flex-shrink-0
                justify-center
                items-center
                mr-4
                focus-within:border-blue-500
            "
        >
            <svg
                class="
                    fill-current
                    hidden
                    w-3
                    h-3
                    text-black
                    pointer-events-none
                "
                version="1.1"
                viewBox="0 0 17 12"
                xmlns="http://www.w3.org/2000/svg"
            >
                <g fill="none" fill-rule="evenodd">
                    <g
                        transform="translate(-9 -11)"
                        fill="black"
                        fill-rule="nonzero"
                    >
                        <path
                            d="m25.576 11.424c0.5658 0.55188 0.56558 1.4439 0 1.9961l-9.404 9.176c-0.28213 0.27529-0.65247 0.41385-1.0228 0.41385-0.37034 0-0-0.13855-1.0228-0.41385l-4.7019-4.588c-0.56584-0.55188-0.56584-1.4442 0-1.9961 0.56558-0.55214 1.4798-0.55214 2.0456 0l3.679 3.5899 8.3812-8.1779c0.56558-0.55214 1.4798-0.5214 2.0456 0z"
                        />
                    </g>
                </g>
            </svg>
        </div>
        <div
            class="text-sm cursor-pointer"
            @click="toggle()"
        >
            {{ label }}
        </div>
        <!-- <label @click="toggle()" class="cursor-pointer">
            <slot></slot>
        </label> -->
    </div>
</template>
<script>
export default {
    name: "FTCheckbox",
    model: {
        prop: "selected",
        event: "change",
    },
    props: {
        selected: {
            checked: {
                type: Array,
                default: () => [],
            },
            disabled: {
                type: Array,
                default: () => [],
            },
        },
        label: {
            type: String,
            default: "",
        },
    },
    methods: {
        toggle() {
            let selected = this.selected;
            if (selected.checked.includes(this.label)) {
                selected.checked = selected.checked.filter(
                    (item) => item !== this.label
                );
            } else {
                selected.checked.push(this.label);
            }
            this.$emit("change", selected);
            console.log(this.selected.checked);
        },
    },
};
</script>
<style lang="postcss" scoped>
input:checked + div {
    @apply border-black;
}
input:checked + div svg {
    @apply block;
}
input:disabled + div {
    @apply border-gray-400;
}
</style>
