<template>
    <div>
        <!-- Register page -->
        <v-card v-if="!loggedIn" class="my-10 mx-auto align-center" max-width="500" elevation="0">
            <v-card-title class="justify-center">
                <p class="text-h4 font-weight-bold primary--text">Register</p>
            </v-card-title>
            <v-card-text>
                <v-form ref="form" v-model="valid" lazy-validation>
                    <v-text-field v-model="email" :rules="emailRules" label="Email" required></v-text-field>
                    <v-text-field
                        v-model="password"
                        :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                        :rules="passwordRules"
                        :type="showPassword ? 'text' : 'password'"
                        label="Password"
                        required
                        @click:append="showPassword = !showPassword"
                    ></v-text-field>
                    <v-text-field
                        v-model="confirmPassword"
                        :append-icon="showConfirmPassword ? 'mdi-eye' : 'mdi-eye-off'"
                        :rules="confirmPasswordRules"
                        :type="showConfirmPassword ? 'text' : 'password'"
                        label="Confirm Password"
                        required
                        @click:append="showConfirmPassword = !showConfirmPassword"
                    ></v-text-field>
                    <v-alert v-if="error" type="error" class="mt-4" elevation="2" border="left" colored-border>
                        {{ errorMsg }}
                    </v-alert>
                    <v-alert v-if="success" type="success" class="mt-4" elevation="2" border="left" colored-border>
                        {{ successMsg }}
                    </v-alert>
                    <v-btn :disabled="!valid" color="primary" class="white--text mt-4" @click="register">
                        Register
                    </v-btn>
                </v-form>
            </v-card-text>
        </v-card>
    </div>
</template>

<script>
    import { mapActions } from 'vuex'
    export default {
        name: 'RwRegister',
        data() {
            return {
                email: '',
                error: false,
                errorMsg: '',
                password: '',
                confirmPassword: '',
                showPassword: false,
                showConfirmPassword: false,
                success: false,
                successMsg: '',
                valid: false,
                emailRules: [(v) => !!v || 'E-mail is required', (v) => /.+@.+\..+/.test(v) || 'E-mail must be valid'],
                passwordRules: [(v) => !!v || 'Password is required'],
                confirmPasswordRules: [
                    (v) => !!v || 'Password is required',
                    (v) => (v && v === this.password) || 'Passwords must match',
                ],
            }
        },
        methods: {
            ...mapActions('user', ['userLogin', 'addAccount', 'userLogout']),
            async register() {
                if (sessionStorage.getItem('api_token') === null) {
                    try {
                        await this.userLogin({
                            application_id: '05d8ab7fc9884950b6f90cad61dc88aa',
                            login_type: 1,
                            login_id: this.email,
                            login_secret: this.password,
                            device_id: 'device_id_1148',
                        })
                        await this.addAccount({
                            addAccount_target_id: sessionStorage.getItem('user_id'),
                            addAccount_target_type: 3,
                        })
                        this.error = false
                        this.success = true
                        this.successMsg = 'Account created successfully, redirecting to home page...'
                        // redirect to home page after 3 seconds
                        setTimeout(() => {
                            this.$router.push('/')
                            this.$router.go()
                        }, 3000)
                    } catch (error) {
                        this.userLogout()
                        this.error = true
                        this.errorMsg = error
                    }
                } else {
                    // already logged in
                    console.log('already logged in')
                }
            },
        },
        computed: {
            // computed
            loggedIn() {
                return sessionStorage.getItem('api_token') !== null
            },
        },
    }
</script>

<style scoped></style>