<template>
    <!-- <div> -->
    <!-- Display the cart info in the drawer -->
    <v-navigation-drawer v-model="drawer" app right temporary width="400px">
        <v-list>
            <v-list-item>
                <v-list-item-content>
                    <v-list-item-title class="title">Cart</v-list-item-title>
                </v-list-item-content>
                <!-- close button -->
                <v-list-item-action>
                    <v-btn icon @click="toggleDrawer">
                        <v-icon>mdi-close</v-icon>
                    </v-btn>
                </v-list-item-action>
            </v-list-item>
            <v-divider></v-divider>
            <v-list-item v-for="(item, index) in cart" :key="index">
                <v-list-item-content>
                    <v-list-item-title>{{ item.data.name }}</v-list-item-title>
                    <v-list-item-subtitle>{{
                        item.data.amounts.primary * item.quantity
                    }}</v-list-item-subtitle>
                </v-list-item-content>
                <v-list-item-action>
                    <!-- <v-btn icon @click="removeFromCart(index)">
                        <v-icon>mdi-delete</v-icon>
                    </v-btn> -->
                    <v-card class="pa-2 text-body-2" elevation="0">
                        <v-row>
                            <v-btn @click="decrementQuantity(index)" icon>
                                <v-icon>mdi-minus</v-icon>
                            </v-btn>
                            <v-card class="pa-2 text-body-2" elevation="0">
                                {{ item.quantity }}
                            </v-card>
                            <v-btn @click="incrementQuantity(index)" icon>
                                <v-icon>mdi-plus</v-icon>
                            </v-btn>
                        </v-row>
                    </v-card>
                </v-list-item-action>
            </v-list-item>
            <v-divider></v-divider>
            <v-list-item>
                <v-list-item-content>
                    <v-list-item-title class="title"
                        >Subtotal</v-list-item-title
                    >
                </v-list-item-content>
                <v-list-item-content>
                    <v-list-item-title class="title"
                        >${{ total }}</v-list-item-title
                    >
                </v-list-item-content>
            </v-list-item>
        </v-list>
        <!-- <template v-slot:append> -->
        <div class="pa-6 text-right">
            <v-btn color="primary" @click="viewCart" class="white--text"
                >View Cart</v-btn
            >
        </div>
        <!-- </template> -->
    </v-navigation-drawer>
    <!-- </div> -->
</template>

<script>
import { mapActions } from "vuex";
import Decimal from "decimal.js";
export default {
    name: "CartDrawer",
    data() {
        return {
            // read cart state from session storage
            cart: JSON.parse(sessionStorage.getItem("cart")) || [],
            // total: 0,
        };
    },
    computed: {
        drawer: {
            get() {
                return this.$store.state.home.drawer;
            },
            set(value) {
                this.$store.commit("home/SET_DRAWER", value);
            },
        },
        total() {
            let total = 0;
            this.cart.forEach((item) => {
                // to number
                // total += Number(item.data.amounts.primary) * item.quantity
                let price = new Decimal(item.data.amounts.primary);
                let quantity = new Decimal(item.quantity);
                total += price.mul(quantity).toNumber();
            });
            return total;
        },
    },
    watch: {
        // update cart display when drawer is opened
        drawer(newValue) {
            if (newValue) {
                this.cart = JSON.parse(sessionStorage.getItem("cart")) || [];
            }
        },
    },
    methods: {
        ...mapActions("home", ["toggleDrawer"]),
        viewCart() {
            // Redirect to cart page
            this.$router.push("/cart");
        },
        removeFromCart(index) {
            // Remove item from cart
            this.cart.splice(index, 1);
            // Update cart in session storage
            sessionStorage.setItem("cart", JSON.stringify(this.cart));
        },
        incrementQuantity(index) {
            // Increment quantity of item in cart
            this.cart[index].quantity++;
            // Update cart in session storage
            sessionStorage.setItem("cart", JSON.stringify(this.cart));
        },
        decrementQuantity(index) {
            // Decrement quantity of item in cart
            if (this.cart[index].quantity > 1) {
                this.cart[index].quantity--;
            }
            // Update cart in session storage
            sessionStorage.setItem("cart", JSON.stringify(this.cart));
        },
    },
};
</script>

<style scoped></style>
