import { userLogin, addAccount, addAddress, createOrder, addSubscriber } from '@/api'

const state = () => ({
    user_id: '',
    api_token: '',
    account_id: '',
})

const getters = {}

const mutations = {
    SET_USER_ID(state, user_id) {
        console.log('@SET_USER_ID: ', user_id)
        state.user_id = user_id
    },
    SET_API_TOKEN(state, token) {
        console.log('@SET_API_TOKEN: ', token)
        state.api_token = token
    },
    SET_ACCOUNT_ID(state, account_id) {
        console.log('@SET_ACCOUNT_ID: ', account_id)
        state.account_id = account_id
    },
}

const actions = {
    // user login
    async userLogin({ commit }, payload) {
        console.log(
            '@action userLogin: ',
            payload.application_id,
            payload.login_type,
            payload.login_id,
            payload.login_secret,
            payload.device_id
        )
        const response = await userLogin(
            payload.application_id,
            payload.login_type,
            payload.login_id,
            payload.login_secret,
            payload.device_id
        )
        console.log('@@@userLogin response: ', response)
        commit('SET_USER_ID', response.user_id)
        sessionStorage.setItem('user_id', response.user_id)
        commit('SET_API_TOKEN', response.api_token)
        sessionStorage.setItem('api_token', response.api_token)
    },
    // add account
    async addAccount({ commit }, payload) {
        console.log('@action addAccount: ', payload.addAccount_target_id, payload.addAccount_target_type)
        const response = await addAccount(payload.addAccount_target_id, payload.addAccount_target_type)
        console.log('@@@addAccount response: ', response)
        commit('SET_ACCOUNT_ID', response.account_id)
        sessionStorage.setItem('account_id', response.account_id)
    },

    // add address
    async addAddress({ commit }, payload) {
        console.log(
            '@action addAddress: ',
            payload.addAddress_parts,
            payload.addAddress_type,
            payload.addAddress_target_id,
            payload.addAddress_target_type
        )
        const response = await addAddress(
            payload.addAddress_parts,
            payload.addAddress_type,
            payload.addAddress_target_id,
            payload.addAddress_target_type
        )
        console.log('@@@addAddress response: ', response)
    },

    // create order
    async createOrder({ commit }, payload) {
        console.log(
            '@action createOrder: ',
            payload.entity_id,
            payload.entity_type,
            payload.createOrder_target_id,
            payload.createOrder_target_type
            // payload.service_area_code,
            // payload.billing_market,
            // payload.billing_sub_market,
            // payload.device_technology_type,
            // payload.msisdn,
            // payload.iccid,
            // payload.imei,
            // payload.imei_type
        )
        const response = await createOrder(
            payload.entity_id,
            payload.entity_type,
            payload.createOrder_target_id,
            payload.createOrder_target_type
            // payload.service_area_code,
            // payload.billing_market,
            // payload.billing_sub_market,
            // payload.device_technology_type,
            // payload.msisdn,
            // payload.iccid,
            // payload.imei,
            // payload.imei_type
        )
        console.log('@@@createOrder response: ', response)
    },

    // add subscriber
    async addSubscriber({ commit }, payload) {
        console.log(
            '@action addSubscriber: ',
            payload.addSubscriber_target_id,
            payload.addSubscriber_target_type,
            payload.subscriber_info
        )
        const response = await addSubscriber(
            payload.addSubscriber_target_id,
            payload.addSubscriber_target_type,
            payload.subscriber_info
        )
        console.log('@@@addSubscriber response: ', response)
    },

    async userLogout({ commit }) {
        console.log('@action userLogout')
        commit('SET_USER_ID', '')
        sessionStorage.removeItem('user_id')
        commit('SET_API_TOKEN', '')
        sessionStorage.removeItem('api_token')
        commit('SET_ACCOUNT_ID', '')
        sessionStorage.removeItem('account_id')
    },
}

export default {
    namespaced: true,
    state,
    mutations,
    actions,
    getters,
}
