import { getAccount, getOrderEntities, getUser } from '@/api'

const state = {
    userId: '',
    userEmail: '',
    MSISDN: '',

    accountIDs: [],
    accountsInfo: [],
    orders: {},
    sortedOrders: [],
}
const mutations = {
    // set state.offers to the response from the API
    SET_USER_ID(state, userId) {
        state.userId = userId
    },
    SET_USER_EMAIL(state, userEmail) {
        state.userEmail = userEmail
    },
    SET_MSISDN(state, MSISDN) {
        state.MSISDN = MSISDN
    },
    SET_ACCOUNT_IDS(state, accountIDs) {
        state.accountIDs = accountIDs
    },
    SET_ACCOUNTS_INFO(state, accountsInfo) {
        state.accountsInfo.push(accountsInfo)
    },
    SET_ORDERS(state, orders) {
        console.log('SET_ORDERS', orders)
        console.log(
            'SET_ORDERS sortedOrders',
            Object.fromEntries(Object.entries(orders).sort((a, b) => b[1].create_timestamp - a[1].create_timestamp))
        )
        state.orders.push(
            Object.fromEntries(Object.entries(orders).sort((a, b) => b[1].create_timestamp - a[1].create_timestamp))
            // Object.entries(orders).sort((a, b) => b[1].create_timestamp - a[1].create_timestamp)
        )
        console.log('state.orders0', state.orders[0])
    },
    RESET_STATE(state) {
        state.userId = ''
        state.userEmail = ''
        state.MSISDN = ''
        state.accountIDs = []
        state.accountsInfo = []
        state.orders = []
    },
}
const actions = {
    async getUser({ commit }, user_id) {
        try {
            const response = await getUser(user_id, 3)
            console.log('@@@getUser: ', response)
            commit('SET_ACCOUNT_IDS', response.user_info.account_ids)
            commit('SET_USER_ID', response.user_info.user_id)

            // find creds with id_type of 'EMAIL_SECRET' and/or 'MSISDN', and set state.userEmail and state.MSISDN
            response.user_info.creds.forEach((cred) => {
                if (cred.id_type === 'EMAIL_SECRET') {
                    commit('SET_USER_EMAIL', cred.id)
                } else if (cred.id_type === 'MSISDN') {
                    commit('SET_MSISDN', cred.id)
                } else {
                    console.log('@@@getUser: cred.id_type not found')
                }
            })
        } catch (error) {
            console.log('@@@getUser error: ', error)
            alert('Error getting user')
        }
    },
    async getAccountsAndOrderInfo({ commit, dispatch }, user_id) {
        commit('RESET_STATE')
        await dispatch('getUser', user_id)
        state.accountIDs.forEach(async (accountID) => {
            // try {
            Promise.all([getAccount(accountID, 2), getOrderEntities(accountID, 2)])
                .then(([accountInfo, orders]) => {
                    console.log('@@@getAccountsAndOrderInfo: ', accountInfo, orders)
                    commit('SET_ACCOUNTS_INFO', accountInfo)
                    commit('SET_ORDERS', orders.orders)
                })
                .catch((error) => {
                    console.log('@@@getAccountsAndOrderInfo error: ', error)
                    alert('Error getting accounts and orders')
                })
                .finally(() => {
                    console.log('@@@getAccountsAndOrderInfo finally')
                })
        })
        console.log('@@@state.orders', state.orders[0])
        state.orders.forEach((order) => {
            console.log('@@@state.orders.forEach: ', order)
            // commit('SET_ORDERS', order)
        })
    },
}
const getters = {}

export default {
    namespaced: true,
    state,
    mutations,
    actions,
    getters,
}
