<script>
import { mapState, mapActions } from "vuex";
export default {
    name: "FTProduct",
    components: {},
    data() {
        return {
            item: [],
            selected: {
                checked: [],
                disabled: [],
            },
            snackbar: false,
            addedItem: null,
            loading: true,
        };
    },
    computed: {
        ...mapState("checkout", {
            // accountError: (state) => state.accountError,
            // numberError: (state) => state.numberError,
            planInfo: (state) => state.planInfo,
        }),
        ...mapState("home", {
            offers: (state) => state.offers,
        }),
    },
    methods: {
        // ...mapActions('cart', ['addToCart']),
        ...mapActions("checkout", ["setPlanInfo"]),
        ...mapActions("home", ["getOffers"]),
        // add to cart
        addToCart(item) {
            // debugger
            this.snackbar = true;
            this.addedItem = item.data.name;
            console.log("@addToCart: ", item);
            // add item to session storage
            let cart = JSON.parse(sessionStorage.getItem("cart"));
            if (cart === null) {
                cart = [];
            }
            // if the item is already in the cart, just add 1 to the quantity, otherwise add the item to the cart and set the quantity to 1
            let index = cart.findIndex((i) => i.id === item.id);
            console.log("index: ", index);
            if (index === -1) {
                item.quantity = 1;
                cart.push(item);
            } else {
                cart[index].quantity += 1;
            }

            // save to session storage
            sessionStorage.setItem("cart", JSON.stringify(cart));
            console.log("cart: ", cart);
        },
        goToCart() {
            this.$router.push({
                name: "RwCart",
            });
        },
    },
    watch: {},
